import { TextField } from "@mui/material";
import React, { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Button1 from "../component/Button1";
import datas from "../data/ContactData.json";
import Button from "../component/contactButton";
import interests from "../data/interest.json";
import "../style/contact.css";
import Footer from "./Footer";
import Navbar1 from "../component/Navbar1";
import sendDatas from "../data/send.json";

const Contact = ({ dark, handleMode }) => {
  const [value, setValue] = useState("");
  const [optionValue, setOptionValue] = useState("");
  const [toValue, setToValue] = useState("");
  const whatsappLink = `https://wa.me/06285831705842?text=Hello I like to do a ${value} ${optionValue} type of project `;
  const emailLink = `mailto: kevinang445566@gmail.com?subject=Project&body=Hello I like to do a ${value} ${optionValue} type of project`;
  const handleClick = (e) => {
    setValue(e.target.value);
  };

  return (
    <div className={dark === true ? "dark" : "light"}>
      <Container
        style={
          dark === true
            ? {
                color: "white",
              }
            : { color: "#112969" }
        }
      >
        <Navbar1 dark={dark} handleMode={handleMode} />
        <Row className="contact-wrapper">
          <div className="d-flex align-items-center justify-content-between my-4 contain">
            <div className="contact ">Contact</div>
            <div>
              Interested in working with me?
              <br /> Submit your project inquiry using the <br /> form below
            </div>
          </div>{" "}
          <div className="my-3 wrapper">
            <div className="name">Interest in ...</div>
            <select
              style={{
                color: "#112969",
              }}
              onChange={(e) => setOptionValue(e.target.value)}
              className="option"
              name=""
              id=""
            >
              {interests.button.map((btn, index) => {
                return (
                  <option defaultValue={"Design"} key={index} value={btn.name}>
                    {btn.name}
                  </option>
                );
              })}
            </select>
          </div>
          <div className="my-3 wrapper">
            <div className="name">To ...</div>
            <select
              style={{
                color: "#112969",
              }}
              onChange={(e) => setToValue(e.target.value)}
              className="option"
              name=""
              id=""
            >
              {sendDatas.map((data, index) => {
                return (
                  <option key={index} value={data.name}>
                    {data.name}
                  </option>
                );
              })}
            </select>
          </div>
          {datas.map((data, index) => {
            return (
              <div className="my-3 wrapper" key={index}>
                <div className="name mb-3">{data.type}</div>
                <div className="contact-button mb-3">
                  {data.button.map((btn, index) => {
                    return (
                      <div key={index}>
                        <Button
                          value={btn.name}
                          text={btn.name}
                          handleClick={handleClick}
                        />
                      </div>
                    );
                  })}
                </div>
              </div>
            );
          })}{" "}
          {/* <div className="my-3 wrapper">
          <div className="name">Project budget (USD)</div>
          <select className="option" name="" id="">
            {prices.button.map((btn) => {
              return <option value="">{btn.name}</option>;
            })}
          </select>
        </div> */}
          <small className="text-muted">
            If you from Upwork , we can discuss there , no need to fill the form
            . the price also negotiable
          </small>
          <div>
            <a href={toValue === "Whatsapp" ? whatsappLink : emailLink}>
              <Button1 text={"Send request"} />
            </a>
          </div>
        </Row>
        <Footer toValue={toValue} value={value} option={optionValue} />
      </Container>
    </div>
  );
};

export default Contact;
