import React from "react";
import Application from "../Application";
import Contact from "../page/Contact";
import KleeImage from "../page/KleeImage";
import About from "../page/About";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useLocation,
} from "react-router-dom";
import { useState } from "react";
import { AnimatePresence } from "framer-motion";
const AnimatedRoute = () => {
  const [dark, setDark] = useState(false);

  const handleMode = () => {
    setDark(!dark);
  };
  const location = useLocation();
  return (
    <div>
      <AnimatePresence>
        <Routes location={location} key={location.pathname}>
          <Route
            index
            element={<Application dark={dark} handleMode={handleMode} />}
          />
          <Route
            path="/about"
            element={<About dark={dark} handleMode={handleMode} />}
          />
          <Route
            path="/contact"
            element={<Contact dark={dark} handleMode={handleMode} />}
          />
          <Route path="/klee" element={<KleeImage />} />
        </Routes>
      </AnimatePresence>
    </div>
  );
};

export default AnimatedRoute;
