import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import React from "react";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import { Image } from "react-bootstrap";
import GitHubIcon from "@mui/icons-material/GitHub";

function NavScrollExample({ dark, handleMode }) {
  const style = {
    backgroundColor: "#eeeff5",
    borderRadius: "1rem",
    opacity: "0.98",
  };
  const colorStyle = {
    color: "#112969",
  };
  return (
    <Navbar
      className="d-flex align-items-center"
      style={style}
      expand="lg"
      variant="light"
    >
      <Container fluid>
        <Navbar.Brand style={colorStyle} href="/">
          KEVIN
        </Navbar.Brand>
        <Navbar.Toggle
          style={{
            border: "none",
            outline: "none",
            backgroundColor: "white",
            width: "20%",
          }}
          aria-controls="navbarScroll"
        />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: "100px" }}
            navbarScroll
          >
            <Nav.Link href="#/about">About</Nav.Link>

            {<Nav.Link href="#/contact">Contact</Nav.Link>}
          </Nav>
          <Nav>
            <Nav.Link
              href="https://www.instagram.com/kevin1234h8/"
              target="_black"
            >
              <Image
                style={{
                  width: "48px",
                }}
                src="https://download.logo.wine/logo/Instagram/Instagram-Logo.wine.png"
              />
            </Nav.Link>
            <Nav.Link>
              <GitHubIcon />
            </Nav.Link>
            <Nav.Link>
              <Image
                style={{
                  width: "24px",
                  marginLeft: "12px",
                }}
                src="https://upload.wikimedia.org/wikipedia/commons/thumb/7/7e/Gmail_icon_%282020%29.svg/2560px-Gmail_icon_%282020%29.svg.png"
              />
            </Nav.Link>
          </Nav>
          <Nav>
            <div className="d-flex ">
              <div
                style={{
                  color: "#112969",
                  marginRight: "10px",
                }}
              >
                {dark === false ? "Light" : "Dark"}
              </div>
              {dark === false ? (
                <Brightness7Icon
                  style={{
                    color: "#112969",
                  }}
                  onClick={handleMode}
                />
              ) : (
                <Brightness4Icon
                  style={{
                    color: "#112969",
                  }}
                  onClick={handleMode}
                />
              )}
            </div>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default NavScrollExample;
